.store {
  position: relative;
  background-color: #f5afb9;

  @media screen and (min-width: 1200px) {
    padding-top: 120px;
  }

  &::before {
    content: '';
    position: absolute;
    top: -20px;
    left: 15%;
    z-index: 2;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url(../images/icons/store-lable.svg);

    @media screen and(min-width: 768px) {
      width: 80px;
      height: 80px;
    }

    @media screen and (min-width: 1200px) {
      top: 7%;
      left: 15%;
      width: 120px;
      height: 120px;
    }
  }
}

.store__btn-link {
  display: block;
  line-height: 1.8;

  & .mas {
    top: 0;
  }
}
